import React, { Component } from 'react';
import {Modal, Button,} from 'react-bootstrap'
import {
  Col,
  FormGroup,
  Input,
  Select,
  Label,
  Row,
} from 'reactstrap';
import jwt_decode from 'jwt-decode';
// import Spinner from '../../Loaders/Spinner'
import './Settings.scss'
import Server_connections from '../../constants/constants';

class ResetPassword extends Component {
    constructor(props) {
      super(props);
      this.state = {
        id: '',
        vaildation_code: '',
        new_password: '',
        token: '',
        email: '',
        message: '',
        isLoaded: false
      }
      this.handleUpdateItem = this.handleUpdateItem.bind(this);
      this.onChange = this.onChange.bind(this);
    }

    onChange(e){
      this.setState({[e.target.name]: e.target.value});
      //console.log({[e.target.name]: e.target.value});
    }

    componentWillReceiveProps(nextProps) {
      this.setState({
        email: nextProps.email
      })
    }

    handleUpdateItem(e){
      e.preventDefault();

      const reset_password = {   
        email: this.state.email,
        vaildation_code: this.state.vaildation_code,
        new_password: this.state.new_password
      };

      fetch(Server_connections.REST_PASSWORD_URL,{
        method: 'POST',
        headers:{
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        body:JSON.stringify(reset_password)
      })
      .then(res => res.json())
      .then((result) =>{
        this.setState({message : result.message, isLoaded : true})
        console.log([this.state.message])
      },
      (error) => {
        this.setState({message : 'Failed.. Try again!!'})
      });

      var { isLoaded } = this.state;

    }
    
    render() {
        return (
            <Modal {...this.props} size="md" aria-labelledby="contained-modal-title-vcenter" centered>
                <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter"><h4>Reset Account Password </h4></Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <h6><span>Check your email for the verification code</span></h6>
                    <FormGroup row className="my-0">
                        <Col xs="12">
                            <FormGroup>
                            </FormGroup>
                        </Col>
                        <Col xs="12">
                            <FormGroup>
                                <Label htmlFor="vaildation_code" className="lable">Verification Code</Label>
                                <Input type="text" name="vaildation_code" placeholder= "Enter Verification Code" value={this.state.vaildation_code} onChange={this.onChange}/>
                            </FormGroup>
                        </Col>
                        <Col xs="12">
                            <FormGroup>
                              <Label htmlFor="new_password" className="lable">New Password</Label>
                              <Input type="text" name="new_password" placeholder= "Enter New Password" value={this.state.new_password} onChange={this.onChange}/>
                            </FormGroup>
                        </Col>
                    </FormGroup>
                    <h6><span className='error-msg'>{this.state.message }</span></h6>
                </Modal.Body>
                <Modal.Footer>
                {/* <div className="d-inline">
                  <Button block color="danger" size="sm" className="btn-pill bg-danger" onClick={this.props.onHide}>Close</Button>
                </div> */}
                <div className="d-inline">
                  <Button block color="success" size="sm" className="btn-pill bg-success" onClick={this.handleUpdateItem}>Rest Now</Button>
                </div>
              </Modal.Footer>
            </Modal>
        )
    }
}

export default ResetPassword;
